import { DataTable } from "primereact/datatable";
import _ObjectUtils from "primereact/components/utils/ObjectUtils";
import React from "react";

class TempTable extends DataTable {
  constructor(props) {
    super(props);
  }
  exportCSV() {
    var _this5 = this;
    var data = this.processData();
    var csv = "\uFEFF";

    var columns = React.Children.toArray(this.props.children); //headers

    for (var i = 0; i < columns.length; i++) {
      if (columns[i].props.field !== this.props.omitColumn) {
        csv += '"' + (columns[i].props.header || columns[i].props.field) + '"';

        if (i < columns.length - 1) {
          csv += this.props.csvSeparator;
        }
      }
    } //body

    data.forEach(function (record, i) {
      csv += "\n";

      for (var _i = 0; _i < columns.length; _i++) {
        var column = columns[_i],
          field = column.props.field;

        if (column.props.exportable && field) {
          var cellData = _ObjectUtils.resolveFieldData(record, field);
          if (cellData != null) {
            if (_this5.props.exportFunction) {
              cellData = _this5.props.exportFunction({
                data: cellData,
                field: field,
              });
            } else cellData = String(cellData).replace(/"/g, '""');
          } else cellData = "";

          csv += '"' + cellData + '"';

          if (_i < columns.length - 1) {
            csv += _this5.props.csvSeparator;
          }
        }
      }
    });
    var blob = new Blob([csv], {
      type: "text/csv;charset=utf-8;",
    });

    if (window.navigator.msSaveOrOpenBlob) {
      navigator.msSaveOrOpenBlob(blob, this.props.exportFilename + ".csv");
    } else {
      var link = document.createElement("a");
      link.style.display = "none";
      document.body.appendChild(link);

      if (link.download !== undefined) {
        link.setAttribute("href", URL.createObjectURL(blob));
        link.setAttribute("download", this.props.exportFilename + ".csv");
        link.click();
      } else {
        csv = "data:text/csv;charset=utf-8," + csv;
        window.open(encodeURI(csv));
      }

      document.body.removeChild(link);
    }
  }
}

class SimpleTable extends React.Component {
  render() {
    return (
      <TempTable
        ref={this.props.tableRef}
        value={this.props.data}
        className="p-datatable-striped datatable-responsive-demo w-full"
        scrollable={true}
        footer={this.props.renderFooter}
        emptyMessage="No Data Found"
        loading={this.props.loading}
        omitColumn="_id"
        {...this.props}
      >
        {this.props.children}
      </TempTable>
    );
  }
}

export default SimpleTable;
